@mixin small {
  @media screen and (min-width: 480px) {
    @content;
  }
}
@mixin medium {
  @media screen and (min-width: 768px) {
    @content;
  }
}
@mixin large {
  @media screen and (min-width: 1200px) {
    @content;
  }
}

@mixin hero__ice-balls {
  @media screen and (max-width: 768px) {
    @content;
  }
}